function createVacancyElements(vacancies) {
  console.log('Vacancies data:', vacancies); // Log the vacancies data

  if (!vacancies || vacancies.length === 0) {
    return <p>No vacancies found.</p>;
  }

  return vacancies.map((vacancy) => {
    console.log(vacancy); // Log the vacancy object

    const {
      id,
      name,
      salary_from,
      salary_to,
      currency,
      area_name,
      published_at,
      employment,
      company_name,
      alternate_url,
    } = vacancy;

    const salary =
      salary_from && salary_to
        ? `${salary_from} - ${salary_to} ${currency}`
        : salary_from
        ? `${salary_from} ${currency}`
        : salary_to
        ? `Up to ${salary_to} ${currency}`
        : '';

    const publishedDate = new Date(published_at).toLocaleDateString();

    return (
      <Vacancy
        key={id}
        name={name}
        company={company_name}
        area={area_name}
        salary={salary}
        employment={employment}
        publishedAt={publishedDate}
        url={alternate_url}
      />
    );
  });
}

export default createVacancyElements;
