async function getOptions() {
    try {
      const response = await fetch('https://api.hh.ru/dictionaries');
      const data = await response.json();
      const options = {
        salary_from: data.salary ? data.salary.map((salary) => ({
          value: salary.from,
          label: `${salary.from} ${salary.currency}`,
        })) : [],
        salary_to: data.salary ? data.salary.map((salary) => ({
          value: salary.to,
          label: `${salary.to} ${salary.currency}`,
        })) : [],
        area_name: data.area ? data.area.map((area) => ({
          value: area.id,
          label: area.name,
        })) : [],
        employment_type: data.employment ? data.employment.map((employment) => ({
          value: employment.id,
          label: employment.name,
        })) : [],
      };
      return options;
    } catch (error) {
      console.error('Error fetching options:', error);
      return {};
    }
  }
export default getOptions;