import React, { useState, useEffect, useCallback } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import styles from '../styles/SideBar.module.scss';
import Select from 'react-dropdown-select';
import '../styles/Select.scss';
import getOptions from '../services/dictionaries';
import getAreas from '../services/areas';

const selectStyle = {
  boxShadow: 'none',
  border: '1px solid #E4E5E7',
  borderRadius: '9px',
  backgroundColor: '#FAFAFB',
};

const Sidebar = ({ onChange }) => {
  const [options, setOptions] = useState([]);
  const [areas, setAreas] = useState([]);
  const [salaryFrom, setSalaryFrom] = useState(null);
  const [salaryTo, setSalaryTo] = useState(null);
  const [selectedArea, setSelectedArea] = useState([]);
  const [selectedEmploymentType, setSelectedEmploymentType] = useState([]);

  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      const options = await getOptions();
      const areas = await getAreas();
      setOptions(options);
      setAreas(areas.sort((a, b) => a.label.localeCompare(b.label))); // sort areas by label

      const query = new URLSearchParams(location.search);
      const salaryFrom = query.get('salary_from');
      const salaryTo = query.get('salary_to');
      const selectedArea = query.get('area_name');
      const selectedEmploymentType = query.get('employment_type');

      if (salaryFrom) {
        setSalaryFrom(salaryFrom);
      }

      if (salaryTo) {
        setSalaryTo(salaryTo);
      }

      if (selectedArea) {
        const areaOption = areas.find((a) => a.value === selectedArea);
        if (areaOption) {
          setSelectedArea([areaOption]);
        } else {
          console.error('Area option not found:', selectedArea);
        }
      }

      if (selectedEmploymentType) {
        if (options.employment) {
          const employmentOption = options.employment.find(
            (e) => e.value === selectedEmploymentType
          );
          if (employmentOption) {
            setSelectedEmploymentType([employmentOption]);
          } else {
            console.error(
              'Employment option not found:',
              selectedEmploymentType
            );
          }
        } else {
          console.error(
            'Employment property not found on options object'
          );
        }
      }
    };
    fetchData();
  }, [location.search]); // add location.search to the dependency array

  const resetFilters = useCallback(() => {
    setSalaryFrom(null);
    setSalaryTo(null);
    setSelectedArea([]);
    setSelectedEmploymentType([]);
  }, []);

  const handleChange = useCallback((field, values) => {
    switch (field) {
      case 'salaryFrom':
        setSalaryFrom(values);
        break;
      case 'salaryTo':
        setSalaryTo(values);
        break;
      case 'area':
        setSelectedArea(values);
        break;
      case 'employmentType':
        setSelectedEmploymentType(values);
        break;
      default:
        break;
    }
    onChange({
      salaryFrom,
      salaryTo,
      selectedArea,
      selectedEmploymentType,
    });
  }, [onChange, salaryFrom, salaryTo, selectedArea, selectedEmploymentType]);

  return (
    <div className={styles.sideBar}>
      <div className={styles.sideBar__header}>
        <div className={styles.sideBar__header__text}>Фильтр</div>
        <div
          className={styles.sideBar__header__reset}
          onClick={() => {
            resetFilters();
            navigate('/');
          }}
        >
          Сбросить
        </div>
      </div>
      <div className={styles.sideBar__area}>
        <div className={styles.sideBar__area__text}>Город</div>
        <Select
          style={selectStyle}
          className="reactDropdownSelect"
          placeholder="Все города"
          options={areas}
          values={selectedArea}
          onChange={(values) => handleChange('area', values)}
        />
      </div>
      <div className={styles.sideBar__area}>
        <div className={styles.sideBar__area__text}>Зарплата</div>
        <div className={styles.sideBar__area__input__container}>
          <span className={styles.sideBar__area__input__text}>От</span>
          <input
            type="text"
            className={styles.sideBar__area__input__element}
            value={salaryFrom || ''}
            onChange={(event) => {
              setSalaryFrom(event.target.value || null);
              handleChange('salaryFrom', event.target.value || null);
            }}
          />
          <span className={styles.sideBar__area__input__text}>До</span>
          <input
            type="text"
            className={styles.sideBar__area__input__element}
            value={salaryTo || ''}
            onChange={(event) => {
              setSalaryTo(event.target.value || null);
              handleChange('salaryTo', event.target.value || null);
            }}
          />
        </div>
      </div>
      <div className={styles.sideBar__area}>
        <div className={styles.sideBar__area__text}>
          Вид занятости
        </div>
        <Select
          style={selectStyle}
          className="reactDropdownSelect"
          placeholder="Не важно"
          options={options.employment}
          values={selectedEmploymentType}
          onChange={(values) => handleChange('employmentType', values)}
        />
      </div>
    </div>
  );
};

export default Sidebar;
