async function getVacancies(
  name,
  page = 0,
  filters = {
    salary_from: null,
    salary_to: null,
    area_name: null,
    employment_type: null,
    title: null,
    url: null,
    currency: null,
    company_name: null,
  }
) {
  try {
    const queryParams = new URLSearchParams();

    if (name) {
      queryParams.set("name", name);
    }

    if (page !== 0) {
      queryParams.set("page", page);
    }

    if (filters.salary_from) {
      queryParams.set("salary_from", filters.salary_from);
    }

    if (filters.salary_to) {
      queryParams.set("salary_to", filters.salary_to);
    }

    if (filters.area_name) {
      queryParams.set("area_name", filters.area_name);
    }

    if (filters.employment_type) {
      queryParams.set("employment_type", filters.employment_type);
    }

    if (filters.title) {
      queryParams.set("title", filters.title);
    }

    if (filters.url) {
      queryParams.set("url", filters.url);
    }

    if (filters.currency) {
      queryParams.set("currency", filters.currency);
    }

    if (filters.company_name) {
      queryParams.set("company_name", filters.company_name);
    }

    const response = await fetch(`http://localhost:3000/api/vacancies?${queryParams.toString()}`);

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const contentType = response.headers.get("Content-Type");
    let data;

    if (contentType && contentType.includes("application/json")) {
      data = await response.json();
      return {
        vacancies: data.items,
        info: {
          totalVacancies: data.found,
          totalPages: Math.ceil(data.found / data.per_page),
        },
      };
    } else {
      const text = await response.text();
      // If the response is not JSON, throw an error or handle it in a different way
      throw new Error(`Invalid content type: ${contentType}`);
    }
  } catch (error) {
    console.error(error);
    throw error;
  }
}

module.exports = { getVacancies };
